<template>
  <div>
    <div v-if="editable" class="mdl-grid">
      <autocomplete
        :data="data"
        :action="action"
        :field="name"
        :multiSelect="false"
        :placeholder="placeholder"
        @selected="setClient"
        >
      </autocomplete>
      <div v-if="displayDetails">
        <form action="POST" novalidate>
          <div >
            <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('name')}">
              <input v-validate="'required|alpha_spaces|min:3'" name="name" id="name" class="mdl-textfield__input" v-model="clientParams.name">
              <label class="mdl-textfield__label" for="name">Name</label>
              <span class="mdl-textfield__error">{{ errors.first('name') }}</span>
            </div>
          </div>
          <div>
            <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('address')}">
              <input v-validate="'required|min:3'" name="address" id="address" class="mdl-textfield__input" v-model="clientParams.address">
              <label class="mdl-textfield__label" for="adress">Address</label>
              <span class="mdl-textfield__error">{{ errors.first('address') }}</span>
            </div>
          </div>
          <div>
            <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('gst_number')}">
              <input v-validate="'required|alpha_num|min:15'" name="gst_number" id="gst_number" class="mdl-textfield__input" v-model="clientParams.gst_number">
              <label class="mdl-textfield__label" for="gst_number">Gst Number</label>
              <span class="mdl-textfield__error">{{ errors.first('gst_number') }}</span>
            </div>
          </div>
          <!-- <button
            type="button"
            class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--primary"
            @click="proceed"
          >
          Proceed
          </button> -->
        </form>
      </div>
    </div>
    <div v-else>
      <div class=" mdl-cell--6-col-desktop	mdl-cell--4-col-phone	">
        <p>{{clientParams.name}}</p>
        <p>{{clientParams.address}}</p>
        <p>{{clientParams.gst_number}}</p>
      </div>
      <div class=" mdl-cell--6-col-desktop	mdl-cell--4-col-phone	">

      </div>
    </div>
  </div>
</template>
<script>
import { Autocomplete } from 'SHARED/autocomplete'
import { ORDER_STATUS } from '@/constants'

export default {
  name: 'order-client',
  components: {
    Autocomplete
  },
  props: {
    data: {
      type: Array,
      required: true
    },
    action: {
      type: Function,
      required: true
    },
    placeholder: {
      type: String,
      default: 'Select from existing'
    },
    name: {
      type: String,
      default: 'name'
    },
    client: {
      type: Object
    },
    fk: {
      type: String,
      required: true
    },
    status: {
      type: String,
      required: true
    },
    dependOnStatus: {
      type: Boolean,
      default: true
    },
    displayDetails: {
      type: Boolean,
      default: true
    }
  },
  updated () {
    window.componentHandler.upgradeAllRegistered()
  },
  computed: {
    clientParams () {
      return this.client || this.params
    },
    editable () {
      if (this.dependOnStatus) {
        return this.status === ORDER_STATUS.initiate
      } else {
        return true
      }
    }
  },
  methods: {
    setParams (data) {
      if (data.id) {}
      this.clientParams[this.fk] = data.id
      this.clientParams.name = data.name
      this.clientParams.gst_number = data.gst_number
      this.clientParams['banks'] = data.banks
      if (data.address_line_1) {
        this.clientParams.address = `${data.address_line_1} ${data.address_line_2}`
      }
    },
    setClient (data) {
      this.setParams(data)
      this.emitValues()
    },
    // proceed () {
    //   this.$validator.validateAll().then(valid => {
    //     if (valid) {
    //       this.emitValues()
    //     }
    //   })
    // },
    valid () {
      return this.$validator.validateAll()
    },
    emitValues () {
      this.$emit('clientSelected', this.clientParams)
    }
  },
  data () {
    return {
      params: {
        name: undefined,
        address: '',
        gst_number: ''
      }
    }
  }
}
</script>
<style lang="scss" scoped>
  .manual-button {
    max-width: 100px;
  }
</style>



